import http from '@/api/http.js'
const app = getApp();
export default {
	data() {
		return {
			imag: app.globalData.imag,
			cut:1,
			upStatus:true,
			searchList:[],
			page:1
		}
	},
	onLoad() {
		this.changeCut(1)
	},
	onReachBottom() {
		
		this.getNewList()
	},
	methods:{
		
		changeCut(cut){
			this.cut = cut
			if(cut == 1){
				this.page=1
				let data = {
					token:uni.getStorageSync('token'),
					
				}
				this.getSearchList(data)
				
			}
			if(cut == 2){
				this.page=1
				let data = {
					token:uni.getStorageSync('token'),
					alsalenum:1
				}
				this.getSearchList(data)
				
			}
			if(cut == 3){
				this.page=1
				let data = {
					token:uni.getStorageSync('token'),
					is_new:1,
					
				}
				this.getSearchList(data)
				
			}
			if(cut == 4){
				this.page=1
				this.upStatus = !this.upStatus
				if(this.upStatus == true){
					
					let data = {
						token:uni.getStorageSync('token'),
						priceSort:1,
					
					}
					this.getSearchList(data)
					
				}else{
					let data = {
						token:uni.getStorageSync('token'),
						priceSort:2,
						
					}
					this.getSearchList(data)
					
				}
			}
		},
		getSearchList(data){
			http('/api/Goods/getMemberGoodsList','post',data).then((res)=>{
				if(res.data.status=='200'){
					this.searchList = res.data.data.goodsList.data
				}else if(res.data.status=='400'){
					this.$util.showToast(res.data.msg)
					return
				}
				console.log(res)
			})
		},
		getNewList(){
			if(this.cut==1){
				
				let data = {
					token:uni.getStorageSync('token'),
					page:this.page+1
				}
				this.getNewListApi(data)
			}
			if(this.cut == 2){
				let data = {
					token:uni.getStorageSync('token'),
					page:this.page+1
				}
				this.getNewListApi(data)
				
			}
			if(this.cut == 3){
				let data = {
					token:uni.getStorageSync('token'),
					is_new:1,
					page:this.page+1
				}
				this.getNewListApi(data)
				
			}
			if(this.cut == 4){
				// this.upStatus = !this.upStatus
				if(this.upStatus == true){
					
					let data = {
						token:uni.getStorageSync('token'),
						priceSort:1,
						page:this.page+1
					}
					this.getNewListApi(data)
					
				}else{
					
					let data = {
						token:uni.getStorageSync('token'),
						priceSort:2,
						page:this.page+1
					}
					this.getNewListApi(data)
					
				}
			}
		},
		getNewListApi(data){
			
			http('/api/Goods/getMemberGoodsList','post',data).then((res)=>{
				
				if(res.data.status=='400'){
					this.$util.showToast(res.data.msg)
					return
				}
				
				if(this.page > res.data.data.goodsList.last_page){
					this.$util.showToast('没有更多了')
					return
				}
				this.page++
				this.searchList = this.searchList.concat(res.data.data.goodsList.data)
			})
		},
		beforeCheck(id){
			let data = {
				token:uni.getStorageSync('token'),
				good_id:id
			}
			http('/api/user/check_good','post',data).then(res=>{
				if(res.data.status == 403){
					uni.navigateTo({
						url:'/pages/viper/vipGoods'
					})
				}else{
					uni.navigateTo({
						url: '/pages/index/detail?goodsid=' + id
					})
				}
			})
		},
		toDetail(id){
			this.beforeCheck(id)
			// uni.navigateTo({
			// 	url:'/pages/index/detail?goodsid='+id
			// })
		},
		back(){
			uni.switchTab({
				url:'/pages/index/index'
			})
		}
	}
}
